<template>
    <div>
        <el-container>
            <el-header class="header">
                <div class="toggle navgation" @click="navMenu">
                    <img :src="navImg" alt="" srcset="" >
                    
                </div>
                <div class="logo-tel">
                    <img src="../assets/index/logo.jpg" alt="" srcset="">
                </div>
                <el-row>
                    <el-col :span="4">
                        <div class=" language" >
                                <span class="cn">简体中文</span>
                                <span class="en">english</span>
                            </div>
                    </el-col>
                    <el-col :span="12"><div class="grid-content bg-purple"></div></el-col>
                    <el-col :span="4">
                        <div class="search">
                                <input class="" type="text" placeholder="搜索">
                            </div>
                    </el-col>
                    <el-col :span="4"><div class="grid-content bg-purple"></div></el-col>
                </el-row>
                
                
            </el-header>
            <div class="navgation-tel" :class="{'is-open':!isOpen}">
                    <div class="search">
                        <input class="" type="text" placeholder="搜索">
                    </div>
                    <el-menu background-color="#1e1e1e" text-color="#ddd" @select="handleSelect">
                    
                    <el-submenu index="1">
                        <template slot="title" class="">关于我们</template>
                        <el-menu-item index="1-1">我们是谁</el-menu-item>
                        <el-menu-item index="1-2">运营理念</el-menu-item>
                        <el-menu-item index="1-3">解决方案</el-menu-item>
                        <el-menu-item index="1-3">联系我们</el-menu-item>
                    </el-submenu>
                    <el-submenu index="2">
                        <template slot="title">合作品牌</template>
                        <el-menu-item index="2-1">选项1</el-menu-item>
                        <el-menu-item index="2-2">选项2</el-menu-item>
                        <el-menu-item index="2-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title">产品一览</template>
                        <el-menu-item index="3-1">选项1</el-menu-item>
                        <el-menu-item index="3-2">选项2</el-menu-item>
                        <el-menu-item index="3-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="4">
                        <template slot="title">最新资讯</template>
                        <el-menu-item index="4-1">选项1</el-menu-item>
                        <el-menu-item index="4-2">选项2</el-menu-item>
                        <el-menu-item index="4-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="5">
                        <template slot="title">资料下载</template>
                        <el-menu-item index="5-1">选项1</el-menu-item>
                        <el-menu-item index="5-2">选项2</el-menu-item>
                        <el-menu-item index="5-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="6" icon="none">
                        <template slot="title">零售展厅</template>
                        <el-menu-item index="6-1">选项1</el-menu-item>
                        <el-menu-item index="6-2">选项2</el-menu-item>
                        <el-menu-item index="6-3">选项3</el-menu-item>
                    </el-submenu>
                </el-menu>


                </div>
            <el-main>
                <div class="logo img-wrap">
                    <img src="../assets/index/logo.jpg" alt="" srcset="">
                </div>
                <div class="navMenu">
                   <el-menu mode="horizontal" @select="handleSelect">
                    
                    <el-submenu index="1">
                        <template slot="title" class="navTitle">关于我们</template>
                        <el-menu-item index="1-1">我们是谁</el-menu-item>
                        <el-menu-item index="1-2">运营理念</el-menu-item>
                        <el-menu-item index="1-3">解决方案</el-menu-item>
                        <el-menu-item index="1-3">联系我们</el-menu-item>
                    </el-submenu>
                    <el-submenu index="2">
                        <template slot="title">合作品牌</template>
                        <el-menu-item index="2-1">选项1</el-menu-item>
                        <el-menu-item index="2-2">选项2</el-menu-item>
                        <el-menu-item index="2-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title">产品一览</template>
                        <el-menu-item index="3-1">选项1</el-menu-item>
                        <el-menu-item index="3-2">选项2</el-menu-item>
                        <el-menu-item index="3-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="4">
                        <template slot="title">最新资讯</template>
                        <el-menu-item index="4-1">选项1</el-menu-item>
                        <el-menu-item index="4-2">选项2</el-menu-item>
                        <el-menu-item index="4-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="5">
                        <template slot="title">资料下载</template>
                        <el-menu-item index="5-1">选项1</el-menu-item>
                        <el-menu-item index="5-2">选项2</el-menu-item>
                        <el-menu-item index="5-3">选项3</el-menu-item>
                    </el-submenu>
                    <el-submenu index="6" icon="none">
                        <template slot="title">零售展厅</template>
                        <el-menu-item index="6-1">选项1</el-menu-item>
                        <el-menu-item index="6-2">选项2</el-menu-item>
                        <el-menu-item index="6-3">选项3</el-menu-item>
                    </el-submenu>
                </el-menu>
                </div>
                <el-carousel indicator-position="none" ref="swiper" :height="swiperStyle.height">
                    <el-carousel-item v-for="item in swiperImg" :key="item">
                        <img :src="item" alt="" srcset="" width="100%" height="100%">
                    </el-carousel-item>
                </el-carousel>


                <div class=" co-brands">
                    <div class="co-brands-title">
                        <div class="adv-title">
                        
                        <p><strong>OUR ADVANTAGE</strong></p>
                        
                        </div>
                        <div class="adv-down">
                            <div class="adv-down-item left ">
                                <div class="line"></div>
                            </div>
                            <div class="adv-down-item adv-down-title">合作品牌</div>
                            <div class="adv-down-item right">
                                <div class="line"></div>
                            </div>
                        </div>
                    </div>
                    <div class="brands">
                            <div class="brand-img" v-for="item in brandImage" :key="item.index">
                                <img :src="item" alt="">
                            </div>
                    </div>
                </div>
                
                <div class="company-introduction">
                    <div class="slogan">
                        <div class="line"></div>
                        <p>我们是谁？</p>
                        <p>我们是南京德朗建材有限公司</p>
                        <p>我们是全球顶级门锁五金产品的直接进口商</p>
                        <p>我们致力于搭建中国市场最优进口门锁五金分享服务平台</p>
                        <p>我们是德朗，我们就在您身边！</p>
                    </div>

                    <div class="employee">
                        <div class="employee-item" v-for="item in employeePic" :key="item.index">
                            <div class="employee-pic">
                                <img :src="item.url" alt="" srcset="" width="100%" height="100%">
                            </div>
                            <div class="employee-name">{{item.name}}</div>
                        </div>
                    </div>
                </div>
                
            </el-main>
            <el-footer>Footer</el-footer>
            
        </el-container>
    </div>
</template>

<script>
    export default {
        name:'index',
        
        data(){
            return {
        activeIndex: '1',
                navImg:require('../assets/index/nav.png'),
                isOpen:true,
                navStyle:{},
                swiperImg:[require('../assets/index/swiper.jpg'),require('../assets/index/swider02.jpg'),require('../assets/index/swiper3.jpg')],
                swiperStyle:{},
                brandImage:['http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792339.jpg',
                           'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792340.jpg',
                           'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792342.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792343.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792344.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792339.jpg',
                           'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792340.jpg',
                           'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792342.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792343.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792344.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792339.jpg',
                           'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792340.jpg',
                           'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792342.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792343.jpg',
                            'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7792344.jpg',
                            ],
                employeePic:[{url:'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7857962.png',name:'a'},
                            {url:'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7857965.jpg',name:'b'},
                            {url:'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7857968.jpg',name:'c'},
                            {url:'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7857971.jpg',name:'d'},
                            {url:'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7857974.jpg',name:'e'},
                            {url:'http://nwzimg.wezhan.cn/contents/sitefiles2028/10143635/images/7857977.jpg',name:'f'}] ,

            }
        },
        methods:{
            handleSelect(key, keyPath) {
                console.log(key, keyPath);
            },
            navMenu(){
                this.isOpen = !this.isOpen
                if(this.isOpen){
                    this.navImg = require('../assets/index/nav.png')
                    
                }else{
                    this.navImg = require('../assets/index/close.png')
                    
                }
            },
            getSwiperStyle(){
                
                this.swiperStyle = {
                    width:this.$refs.swiper.$el.offsetWidth+'px',
                    height:this.$refs.swiper.$el.offsetWidth/2.8+'px'
                }
                if(this.$refs.swiper.$el.offsetWidth/2.8<200){
                    this.swiperStyle.height = 200+'px'
                }
            }
        },
        created(){
            
        },
        mounted(){
            //获取轮播图 图片的高度
            this.getSwiperStyle();
            
            var firstFire = null
            window.onresize=()=>{
                
                this.getSwiperStyle();
                if(firstFire === null) {
                    firstFire = setTimeout(function() {
                        firstFire = null;
                        console.log('当前屏幕宽度',document.documentElement.clientWidth)
                    }, 100);
                }
            }
            



        },
    }
</script>

<style lang="less" scoped>
@media screen and (max-width: 768px){
    .navgation-tel{
        background-color: #1e1e1e;
        color: #ddd;
        &.is-open{
            display: block;
        }
        & .search input{
            color: #ddd;
        }
        & .el-menu{
            border: none;
        }
    }
}
    .el-header{
        padding: 0;
        height: 30px!important;
        line-height: 30px;
        font-family: Tahoma;
        font-size: 12px;
        color: #666;
        background-color: rgba(247, 247, 247, 1);
        & img{
            display: none;
        }
    }
    .grid-content {
        min-height: 30px;
    }
    
    .language{
        display: -webkit-flex; /* Safari */
        display: flex;
        flex-direction:row;
        justify-content:flex-end;
}
    .search{
        width: 100%;
        height: 30px;
        background-color: transparent;
        background-image: url('../assets/index/search_icon.png') ;
        background-repeat: no-repeat;
        background-position: 0 center;
        background-size:contain;
        & input{
            float:left;
            border: none;
            width: calc(100% - 30px);
            height: 30px;
            line-height: 30px;
            padding-left:30px;
            background:none;
        }
        & input:focus{
            outline: none;
        }
    }
    .logo-tel{
        display: none;
    }
    /*移动端目录 */
    .navgation-tel{
        display: none;
            
    }
    /* content */
    .el-main{
        overflow: hidden;
        padding: 0;
        & .logo{
            width: 20%;
            margin: 10px auto 0;
            
            & img{
                width: 110%;
            }
        }
        & .navMenu{
            width: 1200px;
            margin: 0 auto;
            
            & .el-menu{
                display: flex;
                
                & .el-submenu{
                    flex: 1;
                    display: flex;
                    flex-direction: row;
                }
            }
        }
        & .el-carousel{
            
            & .el-carousel__container{
                height: 100%!important;
            }
        }  
    }
    .el-menu.el-menu--horizontal{
        border: none;
    }
    /*合作品牌*/
    .co-brands{
        font-family: Arial,Helvetica,sans-serif;
        & .co-brands-title{
            width: 40%;
            margin: 2rem auto;
            
            text-align: center;
            color: #cfad86;
            & .adv-title{
                font-size: 2rem;
            }
            & .adv-down{
                & .adv-down-item{display: inline-block;position: relative;width: 30%;}
                & .adv-down-title{}
                & .line{
                    border-bottom: 1px solid #999999;
                    position:absolute;
                    top:50%;
                    width: 100%;
                }
            }
        }
        & .brands{
            width: 1200px;
            margin: 0 auto;
            display: flex;
            flex-flow: row wrap;
            
                justify-content:center;
            & img{
                width: 12rem;
                height: 100%;
            }
        }
    }
    /*公司介绍*/
    .company-introduction{
        & .slogan{
            & .line{
                display: block;
                width: 10%;
                border: 1px solid #cfad86;
                margin: 4rem auto 2rem;  
            }
            & p{
                color: #444444;
                text-align: center;
                padding: .5rem 0;
                font-size: 1.2rem
            }
        }
        & .employee {
            display: flex;
            justify-content: space-between;
            
                flex-flow: row wrap;
            & .employee-item{
                margin-bottom: 2rem;
                width: 33%;
                display: flex;
                flex-direction: column;
                & .employee-pic{
                    height: 100%;
                }
                & .employee-name{
                    height: 1rem;
                    text-align: center;
                }
            }
        }
    }
    /* footer */
  .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  /* 屏幕宽度小于768px */
@media only screen and (max-width: 768px){
    .el-header {
        height: 40px!important;
        line-height: 40px;
        background-color: #1e1e1e;
        & .navgation{
            width: 36px;
            height: 100%;
            margin-left: 10px;
            color: #ddd;
            & img{
                display: block;
                width: 100%;
                height: 100%;
            }
            
        }
        
        & .el-row{
            display: none;
            & input{
                color: white;
            }
        }
    
    }
    
    .el-main{
        & .logo{
            width: 40%;
            
        }
        & .navMenu{
            display: none;
        }
    }
}

/* 屏幕小于1200px */
@media screen and (max-width: 1200px){
    .navMenu{
        width: 100%!important;
    }
    .brands{
        width: 100%!important;
        & img{
                width: 10rem!important;
                height: 100%!important;
            }
    }
}




</style>


